import { useNavigate } from "react-router";
import { useEffect } from "react";
const Not = () => {

    const redirect = useNavigate();

    useEffect(() => {
        setTimeout(() => {
            redirect('/');
        }, 5000);
    }, []);

    return (
        <div className="home">
            <h1 className="head">Page Not Found</h1>
            <p className="text-center">Redirecting to home page.....</p>
        </div>
    )
}

export default Not;